import React, {Component} from 'react';
import web3 from '../scripts/web3';
import BlockCard from '../components/BlockCard';
import Layout from '../components/Layout';
import {Table} from 'semantic-ui-react';


class ExplorerIndex extends Component {
    state = {
        blockNumber:0,
    }
    constructor(props) {
        super(props);
        this.tick();
    }
    tick = async () => {
        let number = await web3.eth.getBlockNumber();
        if(number != this.state.blockNumber) {
            this.setState({blockNumber:number});
        } else if(number < this.state.blockNumber) {
            return;
        }
    }
    componentDidMount() {
        this.interval = setInterval(() => this.tick(), 1000);
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }
    render() {
        const {Header,Row,HeaderCell,Body} = Table;
        return(
            <Layout>
                <div>
                    <p>Last block found {this.state.seconds}s ago</p>
                    <link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.2.12/semantic.min.css"></link>
                    <Table>
                        <Header>
                            <Row>
                                <HeaderCell>Time</HeaderCell>
                                <HeaderCell>Block #</HeaderCell>
                                <HeaderCell>Block hash</HeaderCell>
                                <HeaderCell>Tx count</HeaderCell>
                                <HeaderCell>Miner</HeaderCell>
                            </Row>
                        </Header>
                        <Body>
                            <BlockCard currentBlock={this.state.blockNumber}/>
                            <BlockCard currentBlock={this.state.blockNumber-1}/>
                            <BlockCard currentBlock={this.state.blockNumber-2}/>
                            <BlockCard currentBlock={this.state.blockNumber-3}/>
                            <BlockCard currentBlock={this.state.blockNumber-4}/>
                            <BlockCard currentBlock={this.state.blockNumber-5}/>
                            <BlockCard currentBlock={this.state.blockNumber-6}/>
                            <BlockCard currentBlock={this.state.blockNumber-7}/>
                            <BlockCard currentBlock={this.state.blockNumber-8}/>
                            <BlockCard currentBlock={this.state.blockNumber-9}/>
                        </Body>
                    </Table>
                </div>
            </Layout>
        );
    }
}

export default ExplorerIndex;


